import React, { Component } from 'react';
import './Customer.css';
import fire from '../../config/Fire';
import Transaction from './Transaction/Contacts';
import { CSVLink } from "react-csv";
class Material extends Component {

    state = {
        material: [],
        CustomerName: '',
        Cell: '',
        Email: '',
        Notes : '',
        currentUID: fire.auth().currentUser.uid
    }

    // logout function
    logout = () => {
        fire.auth().signOut();
    }

    handleChange = input => e => {
        this.setState({
            [input]: e.target.value !=="0" ? e.target.value : ""
        });
    }

    
    addNewTransaction = () => {
        const {CustomerName, Cell, Email, Notes, currentUID} = this.state;

        // validation
        if(CustomerName && Cell && Email && Notes){

            const BackUpState = this.state.material;
            BackUpState.push({
                id: BackUpState.length + 1,
                name: CustomerName,
                cell: Cell,
                type: Email,
                method: Notes,
                user_id: currentUID
            });
            
            fire.database().ref('Customers/' + currentUID).push({
                id: BackUpState.length,
                name: CustomerName,
                cell: Cell,
                type: Email,
                method: Notes,
                user_id: currentUID
            }).then((data) => {
                //success callback
                console.log('success callback');
                this.setState({
                    material: BackUpState,
                    CustomerName: '',
                    Cell: '',
                    EMail: '',
                    Notes: ''
                })
            }).catch((error)=>{
                //error callback
                console.log('error ' , error)
            });

        }
    }

    componentDidMount(){
        const {currentUID} = this.state;
        const BackUpState = this.state.material;
        fire.database().ref('Customers/' + currentUID).once('value',
        (snapshot) => {
            // console.log(snapshot);
            snapshot.forEach((childSnapshot) => {

                BackUpState.push({
                    id: childSnapshot.val().id,
                    name: childSnapshot.val().name,
                    cell: childSnapshot.val().cell,
                    type: childSnapshot.val().type,
                    method: childSnapshot.val().method,
                    user_id: childSnapshot.val().user_id
                });
                // console.log(childSnapshot.val().name);
            });
            this.setState({
                material: BackUpState
            });
        });
    }

    render(){
        var currentUser = fire.auth().currentUser;
        return(
            <div className="trackerBlock">
                <div className="welcome">
                    <span>Hi, {currentUser.displayName}!</span>
                    <button className="exit" onClick={this.logout}>Logout</button>
                </div>
                <div className="totalMoney">Customers Card</div>

                <div className="newTransactionBlock">
                    <div className="newTransaction">
                        <form>
                            <input
                                onChange={this.handleChange('CustomerName')}
                                value={this.state.CustomerName}
                                placeholder="Enter Customer Name"
                                type="text"
                                name="CustomerName"
                            />
                            <input
                                    onChange={this.handleChange('Cell')}
                                    value={this.state.Cell}
                                    placeholder="Enter Mobile Numer"
                                    type="tel"
                                    name="Cell"
                                />
                            <input
                                    onChange={this.handleChange('Email')}
                                    value={this.state.type}
                                    placeholder="Enter Email Address"
                                    type="email"
                                    name="Email"
                                />    
                            <input
                                    onChange={this.handleChange('Notes')}
                                    value={this.state.Notes}
                                    placeholder="Enter Comments"
                                    type="text"
                                    name="Notes"
                                />
                            
                        </form>
                        <button onClick={() => this.addNewTransaction()} className="addTransaction">+ Add Transaction</button>
                    </div>
                </div>


                <div className="latestTransactions">
                <CSVLink data={this.state.material}>Cust Contacts Download</CSVLink>
                    <ul>
                        {
                            Object.keys(this.state.material).map((id) => (
                                <Transaction key={id}
                                    name={this.state.material[id].name}
                                    cell={this.state.material[id].cell}
                                    type={this.state.material[id].type}
                                    method={this.state.material[id].method}
                                    
                                />
                            ))
                        }
                    </ul>
                </div>
            </div>
        );
    }
}

export default Material;