import React from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Nav from './Nav';
import Main from './components/Main';
import Materials from './components/Matt';
import Plan from './components/Planner';
import Customer from './components/CCard';

//import { PieChart, Pie, Tooltip, BarChart, XAxis, YAxis, Legend, CartesianGrid, Bar } from "recharts";
//https://github.com/remix-run/react-router/blob/main/docs/getting-started/tutorial.md

function App() {
  return (
    <Router>
    <div className="App">
      <Nav />
      <Switch>
      <Route path="/" exact component={Home} />
      <Route path="/main" exact component={Main} />
      <Route path="/material" exact component={Materials} />
      <Route path="/customer" exact component={Customer} />
      <Route path="/todo" exact component={Plan} />
      </Switch>
  </div>
  </Router>
  );
}
const Home = () => (  <div className='Nav-links'>
    <h2>You have visited Home page, Click one of above links to navigate appropriate App</h2>
    </div>


);

export default App;